import SectionStyle from "../styles/section.module.css"
import React from "react"
import cx from "classnames"

export default function Section({
  title,
  background,
  children,
  color,
  noPadding,
}) {
  return (
    <section
      style={{
        background: `url(${background})` || color || "inherit",
        padding: noPadding && "6rem 0",
      }}
      className={SectionStyle.sectioncontainer}
    >
      {title && <h2 className={SectionStyle.title}>{title}.</h2>}

      {children}
    </section>
  )
}

export function BlogListSection({ children }) {
  return <ul className={SectionStyle.BlogListSectionContainer}>{children}</ul>
}

export function BlogSection({ children }) {
  return (
    <section className={SectionStyle.BlogSectionContainer}>
      <div className={SectionStyle.BlogSectionContent}>{children}</div>
    </section>
  )
}

export function Container({ children, padding }) {
  return (
    <div
      className={cx(SectionStyle.container, padding && SectionStyle.Padding)}
    >
      {children}
    </div>
  )
}
