import React from "react"
import NavBar from "../components/NavBar"
import Footer from "../components/Footer"
import { graphql } from "gatsby"
import { BlogSection } from "../components/Section"
import blogpoststyles from "../styles/BlogPost.module.css"

import Img from "gatsby-image"

export default function BlogPost({ data }) {
  console.log("THE ACTULA DARA", data)
  const post = data.markdownRemark

  return (
    <div className={blogpoststyles.container}>
      <NavBar style={{ position: "relative" }} />
      <Img
        className={blogpoststyles.featuredImage}
        fluid={post.frontmatter.featuredImage.childImageSharp.fluid}
      />
      <div className={blogpoststyles.BlogSection}>
        <div className={blogpoststyles.blogContent}>
          <div className={blogpoststyles.header}>
            <h1>{post.frontmatter.title}</h1>
            <p>{post.frontmatter.author} | {post.frontmatter.date}</p>
          </div>
          <hr/>
          <div className={blogpoststyles.body} dangerouslySetInnerHTML={{ __html: post.html }}></div>
        </div>
        <Footer />
      </div>
    </div>
  )
}
export const query = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        title
        author
        date(formatString: "DD MMMM, YYYY")
        featuredImage {
          childImageSharp {
            fluid(maxWidth: 1000) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
      }
    }
  }
`
